@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

*{
    font-family: 'Roboto',sans-serif;
    font-size: 20px;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    top:50%;
    left:50%;
    position: absolute;
    transform:translate(-50%,-50%);
    margin: 0 auto;
    width:256px;
    text-align: center;
    border: 10px solid black;
    background: #101116;

}

.keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
}

input[type="text"]{
    height: 75px;
    width:249px;
    background-color: #10111600;
    border: 0px;
    color:#ffffff;
    text-align: right;
    font-size:25px;
    font-weight: 500;
    letter-spacing: 1px;
}

#clear{
    grid-column: 1/3;
    grid-row: 1;
}

#result{
    grid-column: 3/5;
    grid-row: 5;
}

button{
    margin:3px;
    border:none;
     cursor: pointer;
     font-weight: 800;
}

input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}